@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import './variables.scss';
@import './antd_style_patches.scss';
@import './dark.scss';

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  transition: .2s all ease-in;
  --box-shadow-color: #{ $shadowColor };
}

button {
  cursor: pointer;
}

input, button {
  outline: none;
  background: transparent;
  border: none;
}

.App {
  height: 100%;
  width: 100%;
  position: fixed;
}

.container {
  position: relative;
  overflow: hidden;
  background-color: $templateBackgroundColor;
}

.page {
  position: relative;
  height: 100%;
  // router animations
  &-enter {
    opacity: 0;
    z-index: 2;
    &-active {
      opacity: 1;
      transition: opacity 7000ms;
    }
  }
  &-exit {
    top: 0px;
    position: absolute;
    width: 100%;
    z-index: 1;
    filter: blur(0px);
    opacity: 1;
    &-active {
      filter: blur(100px);
      opacity: 0;
      transition: opacity 7000ms, filter 7000ms;
    }
  }
}

@media screen and (orientation: landscape) {
  .App {
    width: $desktopWidth;
    margin: 0 auto;
    position:relative;
    box-shadow:0 1px 4px var(--box-shadow-color), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  }
  .App:before, .App:after {
    content:"";
      position:absolute;
      z-index:-1;
      box-shadow:0 0 20px rgba(0,0,0,0.8);
      top:10px;
      bottom:10px;
      left:0;
      right:0;
  }
  .App:after {
    right:10px;
    left:auto;
    transform:skew(8deg) rotate(3deg);
  }
  // fixes router animations for desktop
  .page-exit {
    position: absolute;
    .filmCard {
      &__poster__background {
          width: 100% !important;
      }
      &__footer {
        width: 100% !important;
      }
    }
    .settings {
      &__content {
        width: 100% !important;
      }
    }
    .navigation {
      &__container {
        width: 100% !important;
      }
    }
    .lists {
      &__button__group {
        width: 100% !important;
      }
    }
  }
}